/* 1008px */
@media (max-width: 63em) {
  .logo {
    font-size: 5.6rem;
  }
  .logo-text {
    font-size: 2.8rem;
  }
}

/* 848px */
@media (max-width: 53em) {
  .logo {
    font-size: 4.8rem;
  }
  .logo-text {
    font-size: 2.2rem;
  }
}

/* 688px */
@media (max-width: 43em) {
  .App {
    gap: 2rem;
  }
  .logo {
    font-size: 4rem;
  }
  .logo-text {
    font-size: 1.8rem;
  }
  .search-bar {
    height: 2.4rem;
    font-size: 2rem;
  }
}

/* 576px */
@media (max-width: 36em) {
  .logo-section {
    padding-bottom: 0.4rem;
    gap: 0.4rem;
  }
  .logo {
    font-size: 3.2rem;
  }
  .logo-text {
    font-size: 1.2rem;
  }
  .search-bar {
    height: 2rem;
    font-size: 1.6rem;
    width: 30rem;
  }
  .sprite-grid img {
    height: 8rem;
    width: 8rem;
  }
}

/* 416px */
@media (max-width: 26em) {
  .logo-section {
    padding-inline: 1rem;
  }
  .logo {
    font-size: 2.6rem;
  }
  .logo-text {
    font-size: 1rem;
  }
  .search-bar {
    height: 1.8rem;
    font-size: 1.4rem;
    width: 24rem;
  }
  .search-results {
    font-size: 1.4rem;
  }
  .sprite-grid {
    gap: 1.4rem;
    grid-template-columns: 1fr 1fr;
  }
}

/* 320px */
@media (max-width: 20em) {
  .logo {
    font-size: 2.2rem;
  }
  .logo-text {
    font-size: 1rem;
  }
}
