* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(32, 50, 44);
  color: rgb(236, 234, 232);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
}

.logo-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  padding: 3rem;
}

.logo {
  font-size: 6.4rem;
}

.logo-text {
  font-size: 3rem;
}

.search-section {
  position: relative;
}

.search-bar {
  width: 40rem;
  height: 2.8rem;
  font-size: 2.2rem;
}

.search-results {
  position: absolute;
  max-height: 220px; /* Adjust height as needed */
  overflow-y: auto; /* Enable vertical scrolling */
  border: 1px solid #ccc; /* Optional: add border for better visibility */
  background-color: white; /* Optional: background color */
  list-style-type: none; /* Optional: remove bullet points */
  padding: 0; /* Optional: remove padding */
  margin: 0; /* Optional: remove margin */
  color: black;
  z-index: 2;
  width: 100%;
  font-size: 1.8rem;
}

.sprite-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 2rem;
}

.sprite-grid img {
  justify-self: center;
  align-self: center;
  height: 10rem;
  width: 10rem;
}
